/* eslint-disable react/no-danger */
import { useTourEventState } from '@containers/Home/global/TourStats';
import { useGetDetailPost } from '@containers/Home/hook/useGetDetailPost';
import { useKrpano } from '@containers/Home/hook/useKrpano';
import {
  selectDetailProject,
  selectSceneSelected,
} from '@containers/Home/selectors';
import dynamic from 'next/dynamic';
import React, { useEffect, memo } from 'react';
import { useSelector } from 'react-redux';
import { HeaderWrapper } from './styled';

const Logo = dynamic(() => import('@containers/Home/components/Logo'));
const LoadingContainer = dynamic(() => import('@components/LoadingContainer'));
interface Props {
  isShowListScene: boolean;
}

export const Header = memo(({ isShowListScene }: Props) => {
  const detailProject = useSelector(selectDetailProject);
  const sceneSelected = useSelector(selectSceneSelected);

  const [tourEventState] = useTourEventState('state');
  const { onToggleInfoScene, onCloseDialogPostScene } = useKrpano({});

  const { loading, detailPost } = useGetDetailPost({
    idPost: tourEventState.idPostSceneSelected,
  });

  useEffect(() => {
    if (sceneSelected?.post) onToggleInfoScene(true);
    else onToggleInfoScene(false);
    return () => {
      onToggleInfoScene(false);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [sceneSelected]);

  return (
    <HeaderWrapper
      className={`topInfo ${isShowListScene ? 'showSidebar' : ''}`}
    >
      <Logo />
      <div className="--info">
        <h1 className="--projectTitle">{detailProject?.title || '---'}</h1>
        {!!detailProject?.subTitle && (
          <p className="address">{detailProject?.subTitle}</p>
        )}

        <div className="popInfo">
          <button
            className="popInfoToggle"
            type="button"
            onFocus={onToggleInfoScene.bind(this, true)}
            onMouseOver={onToggleInfoScene.bind(this, true)}
          >
            {sceneSelected?.title}
            {sceneSelected?.post && (
              <span>
                <i aria-hidden className="fas fa-info" />
              </span>
            )}
          </button>
          <div
            className={`popInfoContent ${
              tourEventState.visibleDialogPostScene ? 'active' : ''
            }`}
          >
            <LoadingContainer loading={loading}>
              <div className="popInfoContentHead">
                <h4>{sceneSelected?.title}</h4>
                <i
                  className="popInfoClose fal fa-times"
                  onClick={onCloseDialogPostScene}
                />
              </div>
              <div className="popInfoArticle">
                <div
                  dangerouslySetInnerHTML={{ __html: detailPost?.content }}
                />
              </div>
            </LoadingContainer>
          </div>
        </div>
      </div>
    </HeaderWrapper>
  );
});

export default Header;
